import create from "zustand";
import axios from "axios";

const useMateriDetail = create((set) => ({
  data: "",
  loading: false,
  hasErrors: false,
  fetch: async (materi: string = 'go-learn/contents/week-1/hi-go.md') => {
    set(() => ({ loading: true }));
    try {
      const response = await axios.get('https://api.github.com/repos/asruldev/'+ materi, {
        headers: {
          Accept: 'application/vnd.github.v3.raw',
          Authorization: 'token ' + "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"
        },
      })
      set((state: any) => ({ data: (state.data = response.data), loading: false }));
    } catch (err) {
      set(() => ({ hasErrors: true, loading: false }));
    }
  },
}));

export default useMateriDetail;
