import { Container, Spinner } from "@chakra-ui/react";
import { useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";
import Markdown from "../../components/Mdku/Mdku";
import useMateriDetail from "../../databases/useMateriDetails";
import { NoMatch } from "../NoMatch/NoMatch.page";

export function Course() {
  const {pathname} = useLocation();
  const data = useMateriDetail((state: any) => state.data);
  const loading = useMateriDetail((state: any) => state.loading);
  const hasErrors = useMateriDetail((state: any) => state.hasErrors);
  const fetchData = useMateriDetail((state: any) => state.fetch);

  useLayoutEffect(() => {
    const slug = pathname.split("/")[2].replace(/\+/g, "/");
    fetchData(slug);
  }, []);

  if (loading) {
    return <Spinner alignItems={"center"} justifyContent={'center'} />;
  }
  if (hasErrors) {
    return <NoMatch />;
  }

  return (
    <Container maxW={"7xl"}>
      <Markdown>{data}</Markdown>
    </Container>
  );
}
